<template>
  <div class="scrollview">
	  <slot></slot>
	  <div class="weui-loadmore" v-show="!readyToLoad">
		  <i class="weui-loading"></i>
		  <span class="weui-loadmore__tips"></span>
	  </div>
	  <div class="weui-loadmore weui-loadmore_line" v-show="isend">
		  <span class="weui-loadmore__tips"></span>
	  </div>
  </div>
</template>

<script>
export default {
  name: 'scrollView',
  props: {
    readyToLoad: Boolean,
    isend: Boolean
  },
  data () {
    return {
      showOpera: false
    }
  },
  mounted () {
    window.addEventListener('scroll', this.onLoadPage.bind(this))
  },
  beforeDestroy () {
    // scroll
    window.removeEventListener('scroll', this.onLoadPage.bind(this))
  },
  methods: {
    onLoadPage () {
      // clientHeight
      let clientHeight = document.documentElement.clientHeight
      // scrollHeight
      let scrollHeight = document.body.scrollHeight
      // scrollTop
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      // 
      this.$emit('scroll', scrollTop)
      // px
      let porLoadDis = 60
      if ((scrollTop + clientHeight) >= (scrollHeight - porLoadDis)) {
        // 
        if (!this.isend) {
          // api
          if (!this.readyToLoad) {
            return
		  }
          this.$emit('loadCallback')
        }
      }
    }
  }
}
</script>

